<script>
  import VueMomentsAgo from "@/components/VueMoments.vue";
  import FeedbackUp from "@/js/feedbackup/feedbackup";
  import Avatar from "vue-avatar";
  import AddComment from "./AddComment.vue";
  import { bus } from "@/js/core/emit-bus";

  export default {
    components: { VueMomentsAgo, Avatar, AddComment },
    props: {
      feedbackData: Object,
      visitor_id: String,
      fIndex: Number,
      fdb_len: Number, // Feedback data length
    },
    data() {
      return {
        defaultPic: "https://infinity.500apps.com/img/default_avatar.png",
        loading: true,
        activity_data: [],
      };
    },
    async mounted() {
      this.getFeedbackActivity();
      bus.$on("submitComment", this.submitComment);
    },
    destroyed() {
      bus.$off("submitComment", this.submitComment);
    },
    methods: {
      submitComment(response, selected) {
        if (response.length == 1 && !selected) {
          this.$parent.$parent.feed_back_data[this.fIndex].comments_count++;
          this.$parent.$parent.showFeedbackData[this.fIndex].comments_count++;
          this.activity_data.push(response[0]);
        } else {
          this.$set(this.activity_data, this.edit_index, response[0]);
        }
      },
      hide() {
        this.$root.$emit("bv::toggle::collapse", "feedbackDetails");
      },
      next_previous_task(key) {
        if (key == "previous") {
          this.$parent.$parent.rowData = this.$parent.$parent.feed_back_data[this.fIndex - 1];
          this.$parent.$parent.fIndex = this.fIndex - 1;
        } else {
          this.$parent.$parent.rowData = this.$parent.$parent.feed_back_data[this.fIndex + 1];
          this.$parent.$parent.fIndex = this.fIndex + 1;
        }
        this.$parent.$parent.frender++;
      },
      async getFeedbackActivity() {
        this.loading = true;
        let response = await FeedbackUp.getFeedbackDetails(this.feedbackData.id);
        this.loading = false;
        this.activity_data = response;
      },
    },
  };
</script>
